html {
  font-size: 10px;
  line-height: 10px;
  font-weight: 400; }

body {
  font-size: 1.6rem;
  line-height: 2.1rem;
  font-weight: 400;
  margin: 0;
  padding: 0;
  position: relative;
  min-width: 320px;
  margin: 0 auto;
  font-family: "Open Sans", "Arial", sans-serif;
  background-color: #000000;
  color: #ffffff;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh; }

body,
*,
*::before,
*::after {
  box-sizing: border-box; }

main {
  flex-grow: 1; }

img {
  max-width: 100%;
  height: auto;
  display: block; }

@font-face {
  font-family: "PT Sans";
  font-display: swap;
  font-weight: 400;
  font-style: normal;
  src: local("PT Sans"), local("PTSans-Regular"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff"); }

@font-face {
  font-family: "PT Sans";
  font-display: swap;
  font-weight: 700;
  font-style: normal;
  src: local("PT Sans Bold"), local("PTSans-Bold"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff"); }

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden; }

.header {
  padding-top: 67px;
  padding-bottom: 38px; }

.footer {
  padding: 40px 45px; }

.footer__container {
  display: flex;
  flex-wrap: wrap;
  align-items: center; }

.footer__logo {
  margin-left: 132px;
  margin-right: 120px; }

.footer__mail {
  padding: 10px;
  margin-right: auto;
  display: flex;
  justify-content: space-between;
  gap: 10px;
  align-items: center;
  text-decoration: none;
  color: inherit; }

.footer__mail-link {
  margin: 0;
  padding: 0; }

.footer__copyright {
  text-decoration: none;
  color: inherit;
  margin-right: 192px; }

.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  gap: 4px; }

.nav__link {
  display: block;
  text-decoration: none;
  text-transform: capitalize;
  color: inherit;
  padding: 12px 20px;
  border-radius: 10px; }
  .nav__link--accent {
    background-color: #ef4358; }

.team {
  margin-bottom: 184px; }

.team__text {
  font-size: 1.6rem;
  line-height: 3rem;
  font-weight: 400;
  margin: 0 auto 30px;
  padding: 0;
  text-align: center;
  max-width: 800px; }
  .team__text:last-of-type {
    margin-bottom: 80px; }

.team__list {
  list-style: none;
  margin: 0 auto;
  padding: 0;
  max-width: 1200px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 68px; }

.team__item {
  flex-basis: 240px;
  padding-top: 200px;
  position: relative; }
  .team__item::before {
    position: absolute;
    content: "";
    width: 133px;
    height: 133px;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    background-color: #ef4358;
    border-radius: 20px; }
  .team__item::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    background-image: url(../img/team1.png);
    background-repeat: no-repeat;
    top: -30px;
    left: 40px; }
  .team__item:nth-of-type(2)::before {
    background-color: #70c217; }
  .team__item:nth-of-type(2)::after {
    background-image: url(../img/team2.png); }
  .team__item:nth-of-type(3)::before {
    background-color: #ffba49; }
  .team__item:nth-of-type(3)::after {
    background-image: url(../img/team3.png); }
  .team__item:nth-of-type(4)::after {
    background-image: url(../img/team4.png); }
  .team__item:nth-of-type(5)::before {
    background-color: #ffba49; }
  .team__item:nth-of-type(5)::after {
    background-image: url(../img/team5.png); }
  .team__item:nth-of-type(6)::before {
    background-color: #299ef3; }
  .team__item:nth-of-type(6)::after {
    background-image: url(../img/team6.png); }
  .team__item:nth-of-type(7)::before {
    background-color: #70c217; }
  .team__item:nth-of-type(7)::after {
    background-image: url(../img/team7.png); }
  .team__item:nth-of-type(8)::before {
    background-color: #5f37ef; }
  .team__item:nth-of-type(8)::after {
    background-image: url(../img/team8.png); }

.team__member {
  font-size: 2.4rem;
  line-height: 3.3rem;
  font-weight: 700;
  margin: 0;
  padding: 0;
  text-align: center; }

.container {
  max-width: 1492px;
  margin: 0 auto;
  padding: 0 30px;
  box-sizing: content-box; }
  @media (max-width: 1024px) {
    .container {
      min-width: 678px;
      padding: 0 45px; } }
  @media (max-width: 768px) {
    .container {
      min-width: 290px;
      padding: 0 15px; } }

.service {
  margin-bottom: 145px; }

.service__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 70px; }

.service__item {
  padding: 24px 24px 24px 130px;
  border-radius: 20px;
  border: 1px solid #ef4358;
  max-width: 338px;
  min-height: 196px;
  display: flex;
  align-items: center; }

.service__text {
  font-size: 1.9rem;
  line-height: 3rem;
  font-weight: 700;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  display: flex;
  flex-direction: column; }

.line {
  margin-bottom: 158px;
  background-color: #5f37ef;
  min-height: 86px; }

.section-title {
  margin: 0 0 45px;
  padding: 0;
  text-transform: uppercase;
  text-align: center; }
